.blog {
  background-color: #fafafa; }
  .blog .posts {
    padding: 16px;
    max-width: 1080px;
    margin: 8px auto; }
    .blog .posts .card {
      margin: 16px;
      padding: 8px;
      background-color: #fff;
      box-shadow: 0px 4px 12px -4px rgba(0, 0, 0, 0.1); }

.blog-post > .content {
  padding: 32px 16px 16px;
  max-width: 720px;
  margin: 8px auto; }
  .blog-post > .content .by h1 {
    font-size: 4rem;
    font-weight: 300;
    line-height: 1.6em;
    margin-bottom: 32px; }
    .blog-post > .content .by h1::after {
      display: none; }
  .blog-post > .content h1, .blog-post > .content h2, .blog-post > .content h3, .blog-post > .content h4, .blog-post > .content h5, .blog-post > .content h6 {
    margin-bottom: 0.5em;
    position: relative; }
    .blog-post > .content h1::after, .blog-post > .content h2::after, .blog-post > .content h3::after, .blog-post > .content h4::after, .blog-post > .content h5::after, .blog-post > .content h6::after {
      content: "";
      position: absolute;
      display: block;
      height: 2px;
      background-color: #b37feb;
      width: 64px;
      bottom: -2px;
      left: 0; }
  .blog-post > .content p {
    font-size: 18px;
    line-height: 1.8em;
    margin-bottom: 1.8em; }
